.header {
    background-color: #fff;
    padding: 1rem 2rem;
    border-bottom: 1px solid #BFA77B;

    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__menu {
        list-style: none;
        display: flex;
        margin: 0;
        padding: 0;
    }

    &__item {
        margin-right: 1.5rem;

        &:last-child {
            margin-right: 0;
        }
    }

    &__link {
        text-decoration: none;
        color: #3B3B3B;
        font-weight: 500;
        transition: color 0.3s ease;

        &:hover {
            color: #007bff;
            cursor: pointer;
        }
    }
}