
.overlay {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 1000; // 👈 đảm bảo overlay nằm trên các thành phần khá
  width: 100%;
  height: 100%;
}

.modal-parent {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1001;
  background-color: rgba(0, 0, 0, 0.4);
  padding: 30px;
  overflow: auto; // 👈 Thêm dòng này để hiển thị scroll khi modal cao
  /* Ẩn thanh cuộn nhưng vẫn cho phép cuộn */
  scrollbar-width: none;           /* Firefox */
  -ms-overflow-style: none;        /* Internet Explorer 10+ */
  .modal-content {
    background: white;
    padding: 10px;
    border-radius: 8px;
    width: 100%;
    max-width: 90%;
    max-height: 90vh;       // 👈 giới hạn chiều cao tối đa
    overflow-y: auto;       // 👈 cho phép cuộn nội dung khi quá dài
    font-size: 24px;
    box-sizing: border-box;
    position: relative;
    z-index: 1002;           // cao hơn overlay
    
  }

  .modal-content::-webkit-scrollbar {
    display: none;                   /* Chrome, Safari */
  }

  .modal-close {
    position: absolute;
    top: 5px;
    right: 15px;
    border: none;
    background: none;
    font-size: 24px;
    cursor: pointer;
    z-index: 1003;
    
  }
}
