.App {
    /* Beige nhẹ như logo */
    background: linear-gradient(
      90deg,
      #fff8e7,
      #f0e2c2,
      #fff8e7
    ); // màu viền sáng trung tính
    background-size: 300%;
    /* hoặc #E0CDA9 */
    color: #3B3B3B;
    font-family: 'Arial', sans-serif;
    min-height: 100vh;
    max-height: auto;
    animation: borderRun 3s linear infinite;
}

@keyframes borderRun {
    0% {
      background-position: 0% 50%;
    }
    100% {
      background-position: 300% 50%;
    }
  }
