.invoice-container {
    max-width: 1200px;
    margin: 0 auto;
    
  }
  
  .invoice-title {
    text-align: center;
    font-size: 22px;
    font-weight: 700;

  }
  
  .invoice-summary {
    display: flex;
    margin-bottom: 10px;
    align-items: center;
    flex-wrap: nowrap;
    gap: 1rem;

    border-radius: 8px;

    
  }
  
  .summary-item {
  
    font-size: 14px;
    color: #334155;


    .btn-summary {
      padding: 8px 20px;
      border: none;
      border-radius: 8px;
      font-weight: bold;
      cursor: pointer;
      color: white;
      transition: all 0.3s;
      font-size: 14px;
    }
    
    /* Tổng kết: Cam sáng */
    .btn-total {
      background-color: #ff7f50; /* Coral */
    }
    .btn-total:hover {
      background-color: #ff5722; /* Đậm hơn khi hover */
    }
    
    /* Làm mới: Tím xanh */
    .btn-refresh {
      background-color: #6a5acd; /* Slate Blue */
    }
    .btn-refresh:hover {
      background-color: #5a4bb7;
    }
    
    /* Quay lại: Xanh ngọc */
    .btn-back {
      background-color: #20c997; /* Medium Aquamarine */
    }
    .btn-back:hover {
      background-color: #17a589;
    }
    
    
  }

  .refresh {
    width: 100%;
    
  }
  
  .invoice-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
   
    gap: 1.5rem;
  }
  
  .invoice-card {
    background: #fff;
    border: 2px dashed #cbd5e1;
    border-radius: 8px;
    padding: 1.5rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    transition: 0.3s;
  
    &:hover {
      transform: translateY(-5px);
      box-shadow: 0 8px 12px rgba(0, 0, 0, 0.1);
    }
  }
  
  .customer-name {
    font-size: 1.3rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 1rem;
    color: #0f172a;
    text-transform: uppercase;
  }
  
  .invoice-info, .invoice-charges {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    gap: 0.5rem;
    margin-bottom: 1rem;
    .divider {
      display: block;
  width: 100%;
  text-align: center; /* căn giữa nếu cần */
  overflow: hidden;
  white-space: nowrap;
    }
    .invoice-info-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      
    }
  }
  
  .info-line, .charge-line {
    display: flex;
    justify-content: space-between;
    font-size: 0.95rem;
    color: #475569;
    border-bottom: 1px dashed #e2e8f0;
    padding-bottom: 4px;
  }
  
  .total-amount {
    font-size: 1.2rem;
   
    
    text-align: center;
    margin-top: 1rem;
  }
  

  .result {
    padding: 10px;
    .result-item {
      margin: 10px 0;
    }
  }