.search {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 12px;
  margin-top: 30px;

  input {
    flex: 1;
    padding: 10px 14px;
    font-size: 14px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    outline: none;
    transition: 0.3s;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);

    &:focus {
      border-color: #2563eb;
      box-shadow: 0 0 0 2px rgba(37, 99, 235, 0.2);
    }
  }

  button {
    padding: 10px 16px;
    font-size: 14px;
    background-color: #10b981;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: 0.3s;
    margin-top: 0;

    &:hover {
      background-color: #059669;
    }

    &:active {
      background-color: #047857;
    }
  }
}

.title-table {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 12px;
}

.water-table-history-table {
  margin-top: 20px;
  width: 100%;
  border-collapse: collapse;
  font-size: 13px;

  thead {
    background-color: #f1f5f9;
    font-weight: bold;
  }

  th,
  td {
    padding: 10px;
    border: 1px solid #e5e7eb;
    text-align: center;
    .room-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;
    }
    span {
      display: flex;
      justify-content: center;
      align-items: center;
      grid-gap: 10px;
    
    }

    .plus-minus-button {
      width: 18px;
      height: 18px;
      margin-left: 5px;
      border: none;
      border-radius: 6px;
      background-color: #ef4444;
      color: white;
      font-size: 16px;
      font-weight: bold;
      cursor: pointer;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      transition: 0.2s ease;
  
      &.icon {
        width: 20px;
        height: 20px;
        font-size: 13px;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      &:hover {
        background-color: #dc2626;
        transform: scale(1.1);
      }
    }
  }
}

.water-table-pagination {
  display: flex;
  flex-direction: column;
  margin-top: 20px;

  .water-table-notification {
    font-size: 14px;
    margin-bottom: 8px;
  }

  .water-table-controls {
    display: flex;
    justify-content: center;
    gap: 10px;

    button {
      padding: 8px 16px;
      font-size: 14px;
      background-color: #3b82f6;
      color: white;
      border: none;
      border-radius: 6px;
      cursor: pointer;
      transition: 0.3s;

      &:hover {
        background-color: #2563eb;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
      }

      &:active {
        background-color: #1d4ed8;
      }

      &:disabled {
        background-color: #9ca3af;
        cursor: not-allowed;
        box-shadow: none;
      }
    }
  }
}
