.container {
  
  background-color: #f8f8f8;
  border-radius: 15px;
  padding: 10px 30px;
 
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
  font-family: 'Arial', sans-serif;
 
.title {
  text-align: center;
  margin-bottom: 30px;
  font-size: 22px;
  font-weight: bold;
  color: #333;
  text-transform: uppercase;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.formGroup {
  display: flex;
  justify-content: space-between;

  align-items: center;
  gap: 50px;
}

.label {
  font-size: 16px;
  font-weight: bold;
  color: #444;
  display: block;
  margin: 10px 0;
 
}

.input {
  padding: 8px;
  border: 2px solid #ccc;
  border-radius: 8px;
 
  font-size: 16px;
  box-sizing: border-box;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.input:focus {
  border-color: #007bff;
  box-shadow: 0 0 10px rgba(0, 123, 255, 0.5);
}

.input:hover {
  border-color: #0056b3;
}

.control {
  display: flex;
  justify-content: space-between;
  gap: 15px;
  margin-top: 30px;
}

.button {
  padding: 12px 25px;
  background-color: #007bff;
  color: #fff;
  border: none;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s;
}

.button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.button:active {
  transform: scale(1);
}

.message {
  text-align: center;
  color: green;
  margin-top: 20px;
  font-size: 16px;
}

.formGroup span {
  font-size: 16px;
  color: #999;
}


}
