.title-table {
  font-size: 16px;
}
.history-table {
    width: 100%;
    border-collapse: collapse;
    text-align: center;
    font-size: 16px;
    position: relative;
    .add-room {
      position: absolute;
      top: -50px;
      left: 10px;
      width: 130px;
      cursor: pointer;
      z-index: 55;
    }
  
    th, td {
      padding: 8px;
      border: 1px solid #ddd;
      font-size: 13px;
  
  
      span {
        display: block;
      }
    }
  
    thead {
      background-color: #f5f5f5;
    }
  
    button {
      padding: 4px 8px;
      background-color: #dc3545;
      color: white;
      border: none;
      border-radius: 4px;
  
      &:hover {
        background-color: #c82333;
      }
    }
  }
  
  .pagination {
    display: flex;
    flex-direction: column;
  
    .notification {
      margin-top: 3px;
      font-size: 16px;
    }
    .controls {
      display: flex;
      align-items: center;  // Căn giữa theo chiều dọc
      
      button {
        width: 100px;
        padding: 10px;
        
        display: flex;
        align-items: center;  // Căn giữa theo chiều dọc
        justify-content: center;  // Căn giữa theo chiều ngang
        
        cursor: pointer;
        background-color: #3498db;  // Màu xanh lam
        color: white;
        border: 1px solid #ccc;
        border-radius: 4px;
        margin-right: 10px;
      
        &:hover {
          background-color: #2980b9;  // Màu tối hơn khi hover
          box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);  // Bóng đổ lớn hơn khi hover
        }
      
        &:active {
          background-color: #1c6fa3;  // Màu khi nhấn
          box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);  // Bóng đổ nhẹ hơn khi nhấn
        }
      
        &:disabled {
          background-color: #e0e0e0;  // Màu xám khi disabled
          cursor: not-allowed;
          box-shadow: none;  // Không có bóng đổ khi disabled
        } 
      }
    }
  }
  
















