.container {
    padding: 10px;
    background-color: #fff;
    border-radius: 8px;
    
    margin: auto;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);


    .title {
      text-align: center;
      margin-bottom: 20px;
      font-size: 16px;
    
    }
    
    .form {
      display: flex;
      flex-direction: column;
      gap: 15px;
    
    }
    
    .formGroup {
      display: flex;
      flex-direction: column;
    }
    
    .label {
      font-size: 16px;
      margin-bottom: 5px;
      font-weight: bold;
    }
    
    .input {
      padding: 8px;
      border: 1px solid #ccc;
      border-radius: 4px;
    }
    
    .button {
      padding: 10px;
      background-color: #007bff;
      border: none;
      color: white;
      font-weight: bold;
      cursor: pointer;
      border-radius: 5px;
    }
    
    .button:hover {
      background-color: #0056b3;
    }
    
    .message {
      text-align: center;
      color: green;
      margin-top: 10px;
    }
    
  }
  
  