.search {
    width: 100%;
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 30px;
  
    input {
      flex: 1;
      padding: 10px 14px;
      font-size: 14px;
      border: 1px solid #d1d5db;
      border-radius: 8px;
      outline: none;
      transition: 0.3s;
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  
      &:focus {
        border-color: #2563eb;
        box-shadow: 0 0 0 2px rgba(37, 99, 235, 0.2);
      }
    }
  
    button {    
      width: 120px;
      padding: 10px 16px;
      font-size: 14px;
      background-color: #000000;
      color: white;
      border: none;
      border-radius: 8px;
      cursor: pointer;
      transition: 0.3s;
      margin-top: 0;
  
      &:hover {
        background-color: #059669;
      }
  
      &:active {
        background-color: #047857;
      }

      
    }
    .news {
        background-color: #3b82f6;
    }
  }
  
  .title-table {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 12px;
  }
  
  .water-table-history-table {
    margin-top: 20px;
    width: 100%;
    border-collapse: collapse;
    font-size: 14px;
    overflow: scroll;
    thead {
      background-color: #f1f5f9;
      font-weight: bold;
    }
  
    th,
    td {
      padding: 10px;
      border: 1px solid #e5e7eb;
      text-align: center;
      
      span {
        display: block;
        color:green;
        font-weight: 600;
      }
  
    
    }
  }
  
  .water-table-pagination {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
  
    .water-table-notification {
      font-size: 14px;
      margin-bottom: 8px;
    }
  
    .water-table-controls {
      display: flex;
      justify-content: center;
      gap: 10px;
  
      button {
        padding: 8px 16px;
        font-size: 14px;
        background-color: #3b82f6;
        color: white;
        border: none;
        border-radius: 6px;
        cursor: pointer;
        transition: 0.3s;
  
        &:hover {
          background-color: #2563eb;
          box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
        }
  
        &:active {
          background-color: #1d4ed8;
        }
  
        &:disabled {
          background-color: #9ca3af;
          cursor: not-allowed;
          box-shadow: none;
        }
      }
    }
  }
  